import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { chargeMaintenanceroutes } from './charge-maintenance-routing.module';
import { ChargeMaintenanceComponent } from './charge-maintenance.component';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AngularMaterialModule } from 'src/app/shared/angular-material/angular-material.module';
import { NgxSpinnerModule } from 'ngx-spinner';
import { FlexLayoutModule } from '@angular/flex-layout';
import { ChargeMaintenanceNewComponent } from './charge-maintenance-new/charge-maintenance-new.component';
import { ChargeMaintenanceSummaryComponent } from './charge-maintenance-summary/charge-maintenance-summary.component';

@NgModule({
  declarations: [ChargeMaintenanceComponent, ChargeMaintenanceNewComponent, ChargeMaintenanceSummaryComponent],
  imports: [
    CommonModule,
    FormsModule,

    AngularMaterialModule,
    NgxSpinnerModule,
    FlexLayoutModule,
    ReactiveFormsModule,
    RouterModule.forChild(chargeMaintenanceroutes),
  ]
})
export class ChargeMaintenanceModule { }
