import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { chargeMaintenanceDTO } from './chargeMaintenanceDTO';


export const API_URL = AppConstants.acctCloserUrl;
@Injectable({
  providedIn: 'root'
})

export class ChargeMaintenanceServiceService {

  constructor(private http: HttpClient) { }

  onchargeMaintenanceSubmit(maker: string, data) {
    return this.http.post<chargeMaintenanceDTO>(`${API_URL}/charges/save/${maker}`, data);
  }

  onAuthofChargeMaintenance(maker: string, id: number) {
    return this.http.get<chargeMaintenanceDTO>(`${API_URL}/charges/verify/${maker}/${id}`)
  }

  gettingChargeMaintenance() {
    return this.http.get<chargeMaintenanceDTO>(`${API_URL}/charges/getSummary`)
  }
  submittingTheEditedRecord(data, inputBy: string) {
    console.log("in sevice", data);
    return this.http.put<chargeMaintenanceDTO>(`${API_URL}/charges/modify/${inputBy}`, data)
  }
  sendingIdforchargeMaintenanceEdit(id: number) {
    return this.http.get<chargeMaintenanceDTO>(`${API_URL}/charges/getById/${id}`)
  }
  onClosingTheRecord(inputBy: string, id: number) {
    return this.http.get<chargeMaintenanceDTO>(`${API_URL}/charges/close/${inputBy}/${id}`)
  }
  onReopningTheRecord(inputBy: string, id: number) {
    return this.http.get<chargeMaintenanceDTO>(`${API_URL}/charges/reopen/${inputBy}/${id}`)
  }
  onDeletTheRecord(inputBy: string, id: number) {
    return this.http.delete<chargeMaintenanceDTO>(`${API_URL}/charges/delete/${inputBy}/${id}`)
  }

}
